// src/app/signup.tsx
"use client";
import React, { useState } from "react";
import RegisterCard from "@/components/register/register";
import LoginCard from "@/components/login/login";
import { Card, CardContent } from "@/components/ui/card";

export default function SignupPage() {
  const [mode, setMode] = useState<"register" | "login">("register");

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 p-6">
      <Card className="w-full max-w-3xl rounded-2xl shadow-lg">
        <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-6 p-8">
          <div className="hidden md:flex items-center justify-center">
            <div>
              <h1 className="text-3xl font-bold mb-4">{mode === "register" ? "Welcome!" : "Welcome back!"}</h1>
              <p className="text-sm mb-4">
                {mode === "register" ? "Create an account to get started." : "Sign in to continue to your account."}
              </p>

              <button
                onClick={() => setMode(mode === "register" ? "login" : "register")}
                className="underline"
              >
                {mode === "register" ? "Already have an account? Sign in" : "New here? Create account"}
              </button>
            </div>
          </div>

          <div>
            {mode === "register" ? (
              <RegisterCard onRegistered={() => setMode("login")} />
            ) : (
              <LoginCard onLoggedIn={() => alert("Logged in (demo)")} />
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
